import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import styled from "styled-components/macro";

const Shop = () => {
  const products = [
    {
      id: Math.random(),
      name: "Black Solid T-Shirt",
      image:
        "https://outoforder.in/wp-content/uploads/2020/03/mens-black-t-sirt-zoom-01.jpg",
      price: "239",
    },
    {
      id: Math.random(),
      name: "Black Solid Cap",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcScrE_8FKB203hw0tZLFJinL287Th0RZsU0Eg&usqp=CAU",
      price: "199",
    },
    {
      id: Math.random(),
      name: "Black Solid Hoodie",
      image:
        "data:image/jpeg;base64,",
      price: "499",
    },
    {
      id: Math.random(),
      name: "Black Solid Glasses",
      image:
        "https://assets.vogue.com/photos/623dfccfcb16c9c43ba32b24/1:1/w_2667,h_2667,c_limit/slide_14.jpg",
      price: "299",
    },
    {
      id: Math.random(),
      name: "Black Solid T-Shirt",
      image:
        "https://outoforder.in/wp-content/uploads/2020/03/mens-black-t-sirt-zoom-01.jpg",
      price: "239",
    },
    {
      id: Math.random(),
      name: "Black Solid Cap",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcScrE_8FKB203hw0tZLFJinL287Th0RZsU0Eg&usqp=CAU",
      price: "199",
    },
  ];
  return (
    <Container className="text-center">
      <h1
        css={`
          font-size: 32px;
          line-height: 44px;
          letter-spacing: 19px;
        `}
      >
        SHOP
      </h1>
      <Row className="justify-content-between">
        {products.map((item) => {
          return (
            <Col
              key={item.id}
              className="my-5"
              css={`
                height: 400px;
              `}
              md={4}
            >
              <img
                src={item.image}
                alt={item.name}
                css={`
                  width: 100%;
                  height: 100%;
                `}
              />
              <ProductHeading>{item.name}</ProductHeading>
              <ProductPrice>{item.price}</ProductPrice>
            </Col>
          );
        })}
      </Row>
    </Container>
  );
};

export default Shop;

const ProductHeading = styled.h4`
  color: #777;
  font-weight: bold;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  margin-top: 10px;
`;
const ProductPrice = styled.h4`
  color: #949494 !important;
  font-size: 16px;
  line-height: 20px;
  text-transform: none;
  font-family: Lekton, sans-serif;
  font-style: normal;
  font-weight: 600;
  letter-spacing: 0px;
  text-align: center;
`;
