import React from "react";
import "styled-components/macro";
import Slider from "react-slick";

const Portfolio = () => {
  const projects = [
    {
      image:
        "https://static.wixstatic.com/media/86f3ff_93ea1ce64ead43a5a4ee0fd54d538b6e~mv2.jpg/v1/fill/w_400,h_535,al_c,q_80,usm_0.66_1.00_0.01,enc_auto/dog-portrait-tattoo-insta_edited.jpg",
      name: "Animal Tattoo",
    },
    {
      image:
        "https://static.wixstatic.com/media/86f3ff_93ea1ce64ead43a5a4ee0fd54d538b6e~mv2.jpg/v1/fill/w_400,h_535,al_c,q_80,usm_0.66_1.00_0.01,enc_auto/dog-portrait-tattoo-insta_edited.jpg",
      name: "Religious Tattoos",
    },
    {
      image:
        "https://static.wixstatic.com/media/86f3ff_982efa7aabfa470b9cb20c4017df53ea~mv2.jpg/v1/fill/w_400,h_535,al_c,q_80,usm_0.66_1.00_0.01,enc_auto/Couple%20Tattoo%20Aliens%20Tattoo.jpg",
      name: "Couple Tattoos",
    },
    {
      image:
        "https://static.wixstatic.com/media/86f3ff_982efa7aabfa470b9cb20c4017df53ea~mv2.jpg/v1/fill/w_400,h_535,al_c,q_80,usm_0.66_1.00_0.01,enc_auto/Couple%20Tattoo%20Aliens%20Tattoo.jpg",
      name: "Small Tattoos",
    },
    {
      image:
        "https://static.wixstatic.com/media/86f3ff_5cabe6d979eb4d718626cbdf54827212~mv2.jpg/v1/fill/w_400,h_535,al_c,q_80,usm_0.66_1.00_0.01,enc_auto/Portrait%2520Tattoo%2520At%2520Aliens%2520Tattoo.jpg",
      name: "Portrait Tattoos",
    },
  ];
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 3,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
        },
      },
    ],
  };
  return (
    <>
      <p
        className="text-center my-5"
        css={`
          font-size: 2.5rem;
          font-family: "Poppins", sans-serif;
          font-weight: bold;
        `}
      >
        Our Previous Work
      </p>
      <Slider {...settings}>
        {projects.map((item) => {
          return (
            <div
              css={`
                border-radius: 25px;
              `}
              key={item.name}
            >
              <img
                css={`
                  border-radius: 25px;
                `}
                src={item.image}
                alt={item.name}
                className="w-100 h-100"
              />
              <h4 className="text-center">{item.name}</h4>
            </div>
          );
        })}
      </Slider>
    </>
  );
};

export default Portfolio;
